<template>
  <div class="test">
    <v-logo></v-logo>
    <p v-text="msg"></p>
  </div>
</template>
<script>
  import logo from './logo.vue'
  /*data是函数！真正的值需要放在return的返回值中*/
  export default{
    data(){
     return{
       msg:'I am from test.vue'
     }
    },
    components:{
      'v-logo':logo
    }
  }
</script>

<style>
  .test{display: flex;flex-direction: column;align-items: center;}
  .test p{color: #00f}
</style>


